/**
 * @WordPress dependencies
 */

import { memo } from '@wordpress/element';

/**
 * @others dependencies
 */
import classnames from 'classnames';

const blockName = 'ark-block-slider';

/**
 * 背景画像のソース
 */
export const SlideMedia = memo(({ variation, media, mediaSP, focalPoint, focalPointSP }) => {
	const mediaUrl = media.url;
	const mediaUrlSP = mediaSP.url;

	if (!mediaUrl) {
		return null;
	}

	const isRichSlider = 'rich' === variation;

	const videoProps = isRichSlider
		? { autoPlay: '1', loop: '1', playsInline: '1', muted: '1' }
		: { controls: '1' };

	let slideMediaPC = null;
	if (mediaUrl) {
		const mediaProps = {
			src: mediaUrl,
			width: media.width || null,
			height: media.height || null,
		};

		if (!!focalPoint) {
			const pX = (focalPoint.x * 100).toFixed();
			const pY = (focalPoint.y * 100).toFixed();
			mediaProps.style = { objectPosition: `${pX}% ${pY}%` };
		}

		if ('video' === media.type) {
			slideMediaPC = (
				<video
					{...mediaProps}
					className={classnames(`${blockName}__video arkb-obf-cover`, {
						'arkb-only-pc': !!mediaSP.type,
					})}
					{...videoProps}
				></video>
			);
		} else if ('image' === media.type) {
			slideMediaPC = (
				<img
					{...mediaProps}
					alt={media.alt || ''}
					className={classnames(`${blockName}__img arkb-obf-cover`, {
						'arkb-only-pc': !!mediaSP.type,
					})}
				/>
			);
		}
	}

	let slideMediaSP = null;
	if (mediaUrlSP) {
		const mediaProps = {
			src: mediaUrlSP,
			width: mediaSP.width || null,
			height: mediaSP.height || null,
		};

		if (!!focalPointSP) {
			const pX = (focalPointSP.x * 100).toFixed();
			const pY = (focalPointSP.y * 100).toFixed();
			mediaProps.style = { objectPosition: `${pX}% ${pY}%` };
		}

		if ('video' === mediaSP.type) {
			slideMediaSP = (
				<video
					{...mediaProps}
					className={`${blockName}__video arkb-obf-cover arkb-only-sp`}
					{...videoProps}
				></video>
			);
		} else if ('image' === mediaSP.type) {
			slideMediaSP = (
				<img
					{...mediaProps}
					alt={mediaUrlSP.alt || ''}
					className={`${blockName}__img arkb-obf-cover arkb-only-sp`}
				/>
			);
		}
	}

	const layerClass = classnames(`${blockName}__media`, {
		'arkb-absLayer': isRichSlider,
	});

	return (
		<div className={layerClass}>
			{slideMediaPC}
			{slideMediaSP}
		</div>
	);
});
