/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useCallback } from '@wordpress/element';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Button, TextControl, FocalPointPicker } from '@wordpress/components';
import { Icon, video, image } from '@wordpress/icons';

/**
 * @Inner dependencies
 */
import { DEFAULT_ATTRS } from '../config';
import { ArkbDeviceTab, ArkbImageSizeSelect } from '@components';

const MediaPreview = ({ useFocalPicker, url, type, focalPoint, setFocalPoint }) => {
	if (!url) {
		return (
			<div className='arkb-imgPreview -noimage'>
				<Icon icon={image} /> / <Icon icon={video} />
			</div>
		);
	}

	if (useFocalPicker) {
		return <FocalPointPicker url={url} value={focalPoint} onChange={setFocalPoint} />;
	}
	return (
		<div className='arkb-imgPreview'>
			{'image' === type && <img src={url} alt='' />}
			{'video' === type && <video src={url} />}
		</div>
	);
};

const getNewMediaAttrs = (newMedia) => {
	return {
		id: newMedia.id,
		url: newMedia.url,
		alt: newMedia.alt,
		type: newMedia.type,
		width: newMedia.width,
		height: newMedia.height,
		size: undefined,
	};
};

/**
 * export ImageTab
 */
export default ({ attrs, setAttributes }) => {
	const { isRichSlider, media, mediaSP, focalPoint, focalPointSP, opacity } = attrs;
	const mediaUrl = media.url;
	const mediaUrlSP = mediaSP.url;

	// mediaデータのアップデート
	const setMedia = (device, newAttrs) => {
		if ('pc' === device) {
			setAttributes({
				media: {
					...media,
					...newAttrs,
				},
			});
		} else if ('sp' === device) {
			setAttributes({
				mediaSP: {
					...mediaSP,
					...newAttrs,
				},
			});
		}
	};

	const removeMedia = (device) => {
		if ('pc' === device) {
			setAttributes({
				media: DEFAULT_ATTRS.media,
				focalPoint: undefined,
			});
		} else if ('sp' === device) {
			setAttributes({
				mediaSP: DEFAULT_ATTRS.media,
				focalPointSP: undefined,
			});
		}
	};

	// サイズを変えた時の処理
	const updateImageSizePC = useCallback(
		(newSizeSlug, newSizeData) => {
			setMedia('pc', {
				size: newSizeSlug,
				url: newSizeData?.source_url,
				width: newSizeData?.width,
				height: newSizeData?.height,
			});
		},
		[media]
	);

	const updateImageSizeSP = useCallback(
		(newSizeSlug, newSizeData) => {
			setMedia('sp', {
				size: newSizeSlug,
				url: newSizeData?.source_url,
				width: newSizeData?.width,
				height: newSizeData?.height,
			});
		},
		[mediaSP]
	);

	const imageSettingPC = (
		<>
			<MediaPreview
				useFocalPicker={isRichSlider}
				url={mediaUrl}
				type={media.type || ''}
				focalPoint={focalPoint}
				setFocalPoint={(val) => {
					setAttributes({ focalPoint: val });
				}}
			/>
			<div className='arkb-btns--media'>
				<MediaUploadCheck>
					<MediaUpload
						onSelect={(newMedia) => {
							if (newMedia) {
								setMedia('pc', getNewMediaAttrs(newMedia));
								if (100 === opacity) {
									setAttributes({ opacity: undefined });
								}
							} else {
								removeMedia('pc');
							}
						}}
						allowedTypes={['image', 'video']}
						value={media.id}
						render={({ open }) => (
							<Button
								text={
									mediaUrl
										? __('Change media', 'arkhe-blocks')
										: __('Select media', 'arkhe-blocks')
								}
								isPrimary
								onClick={open}
							/>
						)}
					/>
				</MediaUploadCheck>
				{mediaUrl && (
					<Button
						text={__('Delete', 'arkhe-blocks')}
						isSecondary
						className='__delete'
						onClick={() => {
							removeMedia('pc');
						}}
					/>
				)}
			</div>
			{!media?.id && (
				<TextControl
					className='u-arkb--mt--10'
					label={__('Media URL', 'arkhe-blocks')}
					value={mediaUrl || ''}
					onChange={(val) => {
						const mediaType = val.match(/\.(mp4|avi|fiv|mov|wmv)/i) ? 'video' : 'image';
						setMedia('pc', { id: 0, url: val, type: mediaType });
					}}
				/>
			)}
			{0 !== media.id && 'image' === media.type && (
				<ArkbImageSizeSelect
					className='arkb-ctrl--mt--s'
					imgId={media.id}
					imgSize={media.size}
					onChange={updateImageSizePC}
				/>
			)}
			{!isRichSlider && 'image' === media.type && (
				<TextControl
					label={__('Alt text', 'arkhe-blocks')}
					value={media.alt || ''}
					onChange={(val) => {
						setMedia('pc', { alt: val });
					}}
				/>
			)}
		</>
	);

	//メディアプレビュー
	const imageSettingSP = (
		<>
			<MediaPreview
				useFocalPicker={isRichSlider}
				url={mediaUrlSP}
				type={mediaSP.type || ''}
				focalPoint={focalPointSP}
				setFocalPoint={(val) => {
					setAttributes({ focalPointSP: val });
				}}
			/>
			<div className='arkb-btns--media'>
				<MediaUploadCheck>
					<MediaUpload
						onSelect={(newMedia) => {
							if (newMedia) {
								setMedia('sp', getNewMediaAttrs(newMedia));
							} else {
								removeMedia('sp');
							}
						}}
						// allowedTypes={[media.type]}
						allowedTypes={['image', 'video']}
						value={mediaSP.id}
						render={({ open }) => (
							<Button
								text={
									mediaUrlSP
										? __('Change media', 'arkhe-blocks')
										: __('Select media', 'arkhe-blocks')
								}
								isPrimary
								onClick={open}
							/>
						)}
					/>
				</MediaUploadCheck>
				{mediaUrlSP && (
					<Button
						text={__('Delete', 'arkhe-blocks')}
						isSecondary
						className='__delete'
						onClick={() => {
							removeMedia('sp');
						}}
					/>
				)}
			</div>
			{!mediaSP?.id && (
				<TextControl
					className='u-arkb--mt--10'
					label={__('Media URL', 'arkhe-blocks')}
					value={mediaUrlSP || ''}
					onChange={(val) => {
						const mediaType = val.match(/\.(mp4|avi|fiv|mov|wmv)/i) ? 'video' : 'image';
						setMedia('sp', { id: 0, url: val, type: mediaType });
					}}
				/>
			)}
			{0 !== mediaSP.id && 'image' === mediaSP.type && (
				<ArkbImageSizeSelect
					className='arkb-ctrl--mt--s'
					imgId={mediaSP.id}
					imgSize={mediaSP.size}
					onChange={updateImageSizeSP}
				/>
			)}
			{!isRichSlider && 'image' === mediaSP.type && (
				<TextControl
					label={__('Alt text', 'arkhe-blocks')}
					value={mediaSP.alt || ''}
					onChange={(val) => {
						setMedia('sp', { alt: val });
					}}
				/>
			)}
		</>
	);

	let addClass = `-media`;
	if (!mediaUrl) {
		addClass += ' has-no-pcimg';
	}

	return (
		<ArkbDeviceTab className={addClass} controlPC={imageSettingPC} controlSP={imageSettingSP} />
	);
};
