/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { BlockControls, useBlockProps, MediaPlaceholder } from '@wordpress/block-editor';
import { forwardRef } from '@wordpress/element';
import { BlockAlignmentMatrixControl, useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import { mediaIcon } from '../slider/icon';
import { SlideMedia } from './components/SlideMedia';
import { convertStylesToCssCode, getCusomPadding } from '@helper';
import { DynamicStyles } from '@components';

/**
 * @Others dependencies
 */
// import classnames from 'classnames';

/**
 * const
 */
const blockName = 'ark-block-slider';

function getDynamicStyles({ paddingPC, paddingSP, clientId }) {
	const styles = {
		all: {
			'--arkb-padding': paddingPC ? getCusomPadding(paddingPC, '0') : '',
		},
		sp: {
			'--arkb-padding': paddingSP ? getCusomPadding(paddingSP, '0') : '',
		},
	};

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}

/**
 * Media Slider
 */
export const MediaEdit = forwardRef(({ attributes, setAttributes }, ref) => {
	const { variation, media, mediaSP, focalPoint, focalPointSP } = attributes;

	const mediaId = media.id;
	const mediaUrl = media.url;

	// BlockProps
	const blockProps = useBlockProps({
		className: `${blockName}__slide`,
		ref,
	});

	const onSelect = (newMedia) => {
		setAttributes({
			media: {
				id: newMedia?.id || 0,
				url: newMedia?.url || '',
				alt: newMedia?.alt || undefined,
				type: newMedia?.type || undefined,
				width: newMedia?.width || undefined,
				height: newMedia?.height || undefined,
			},
		});
	};

	const onSelectURL = (newURL) => {
		const mediaType = newURL.match(/\.(mp4|avi|fiv|mov|wmv)/i) ? 'video' : 'image';
		setAttributes({
			media: {
				id: 0,
				url: newURL || '',
				type: mediaType,
			},
		});
	};

	return (
		<>
			<div {...blockProps}>
				{mediaUrl ? (
					<SlideMedia {...{ variation, media, mediaSP, focalPoint, focalPointSP }} />
				) : (
					<MediaPlaceholder
						className='is-large'
						labels={{
							title: __('Media', 'arkhe-blocks'),
						}}
						icon={mediaIcon}
						onSelect={onSelect}
						onSelectURL={onSelectURL}
						accept='image/*,video/*'
						allowedTypes={['image', 'video']}
						value={{ mediaId, mediaUrl }}
						disableMediaButtons={mediaUrl}
					/>
				)}
			</div>
		</>
	);
});

/**
 * Rich Slider
 */
export const RichEdit = forwardRef(
	({ attributes, setAttributes, clientId, isHideContent }, ref) => {
		const {
			variation,
			media,
			mediaSP,
			focalPoint,
			focalPointSP,
			bgColor,
			bgGradient,
			opacity,
			textColor,
			contentPosition,
			paddingPC,
			paddingSP,
			filter,
		} = attributes;

		// BlockProps
		const blockProps = useBlockProps({
			className: `${blockName}__slide`,
			ref,
		});

		const dynamicStyles = getDynamicStyles({ paddingPC, paddingSP, clientId });

		const bgOpacity = undefined === opacity ? 50 : opacity;
		const colorLayerStyle = {
			background: bgGradient || bgColor,
			opacity: (bgOpacity * 0.01).toFixed(2),
		};
		const txtLayerStyle = {};
		if (textColor) {
			txtLayerStyle.color = textColor;
		}
		const innerBlocksProps = useInnerBlocksProps(
			{ className: `${blockName}__bodyInner ark-keep-mt--s`, style: txtLayerStyle },
			{
				// template: [['core/paragraph']],
				templateLock: false,
			}
		);

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				{!isHideContent && (
					<BlockControls group='block'>
						<BlockAlignmentMatrixControl
							label={__('Change content position')}
							value={contentPosition}
							onChange={(nextPosition) => {
								setAttributes({ contentPosition: nextPosition });
							}}
						/>
					</BlockControls>
				)}
				<div {...blockProps}>
					<SlideMedia {...{ variation, media, mediaSP, focalPoint, focalPointSP }} />
					<div
						className={`${blockName}__color arkb-absLayer`}
						style={colorLayerStyle}
					></div>
					{'off' !== filter && (
						<div className={`c-filterLayer -filter-${filter} arkb-absLayer`}></div>
					)}
					<div
						className={`${blockName}__body`}
						data-content={contentPosition.replace(' ', '-')}
						style={txtLayerStyle}
					>
						{isHideContent ? (
							<div className='__hide-content'>
								{__('The first slide content will be displayed.', 'arkhe-blocks')}
							</div>
						) : (
							<div {...innerBlocksProps} />
						)}
					</div>
				</div>
			</>
		);
	}
);
