/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { useSelect } from '@wordpress/data';
import { InnerBlocks, InspectorControls, BlockControls } from '@wordpress/block-editor';
import { ToolbarButton, ToolbarGroup } from '@wordpress/components';
import { useEffect, useContext, useRef } from '@wordpress/element';
import { closeSmall } from '@wordpress/icons';

/**
 * @Internal dependencies
 */
import blockIcon from './icon';
import metadata from './block.json';
import SlideSidebar from './_sidebar';
import { MediaEdit, RichEdit } from './edit';
import { DEFAULT_ATTRS } from './config';
import { ArkbLinkControl } from '@components';

/**
 * フォーカスしたスライドへスクロール
 */
const scrollToSelectedSlide = (clientId, direction, ref) => {
	const { ownerDocument } = ref.current;

	const me = ownerDocument.querySelector(`#block-${clientId}`);
	if (null === me) return;

	const parentNode = me.parentNode;
	if (null === parentNode) return;

	const firstNode = parentNode.childNodes[0];

	if ('vertical' === direction) {
		const offset = firstNode ? firstNode.offsetTop : 0;
		parentNode.scrollTop = me.offsetTop - offset;
	} else {
		const offset = firstNode ? firstNode.offsetLeft : 0;
		parentNode.scrollLeft = me.offsetLeft - offset;
	}
};

/**
 * Register Block
 */
registerBlockType(metadata.name, {
	title: __('Slider content', 'arkhe-blocks'),
	icon: blockIcon,
	attributes: metadata.attributes,

	edit: ({ attributes, setAttributes, clientId, isSelected }) => {
		const { variation, media, mediaSP, href, rel, isNewTab, ariaLabel } = attributes;

		// Contextの取得
		const { SliderContext } = window.arkbContext;
		const context = useContext(SliderContext);

		const { parentID, childIDs, setActSlide, removeSlide, direction, fixFirst } = context;

		const ref = useRef(null);

		const { myIndex, isLast } = useSelect(
			(select) => {
				const _index = select('core/block-editor').getBlockIndex(clientId, parentID);
				return {
					myIndex: _index,
					isFirst: _index === 0,
					isLast: _index === childIDs.length - 1,
				};
			},
			[clientId, parentID, childIDs]
		);

		// 古い形式のデータ等を置換
		useEffect(() => {
			if (attributes.alt) {
				setAttributes({ media: { ...media, alt: attributes.alt } });
			}

			// PCメディアがないのにSPメディアのデータがある場合、削除。
			if (!media.url && !!mediaSP.url) {
				setAttributes({
					mediaSP: DEFAULT_ATTRS.media,
					focalPointSP: undefined,
				});
			}
		}, []);

		// アクテイブなスライドのセット
		useEffect(() => {
			if (isSelected) {
				scrollToSelectedSlide(clientId, direction, ref);
				setActSlide(myIndex);
			}
		}, [clientId, isSelected, childIDs, direction]);

		// コンテンツを隠すかどうか
		const isHideContent = fixFirst && 0 < myIndex;

		const setLink = (changedVal) => {
			const { url, rel: newRel, opensInNewTab } = changedVal;

			if (url) {
				setAttributes({ href: url || undefined });
			}

			if (typeof opensInNewTab !== 'undefined') {
				setAttributes({
					isNewTab: opensInNewTab,
					rel: newRel,
				});
			}
		};

		const removeLink = () => {
			setAttributes({
				href: undefined,
				isNewTab: false,
				rel: undefined,
				ariaLabel: undefined,
			});
		};
		const setAriaLabel = (val) => {
			setAttributes({ ariaLabel: val || undefined });
		};

		return (
			<>
				<BlockControls>
					{!isHideContent && (
						<ArkbLinkControl
							attrs={{ url: href, rel, isNewTab, ariaLabel }}
							setLink={setLink}
							removeLink={removeLink}
							setRel={(val) => {
								setAttributes({ rel: val || undefined });
							}}
							setAriaLabel={'rich' === variation ? setAriaLabel : null}
						/>
					)}
					<ToolbarGroup>
						<ToolbarButton
							className='components-toolbar__control arkb-toolBtn--deleteSlide'
							label={__('Delete this slide', 'arkhe-blocks')}
							icon={closeSmall}
							onClick={() => {
								removeSlide(myIndex, isLast);
							}}
						>
							{__('Delete this slide', 'arkhe-blocks')}
						</ToolbarButton>
					</ToolbarGroup>
				</BlockControls>
				<InspectorControls>
					<SlideSidebar {...{ attributes, setAttributes, clientId }} />
				</InspectorControls>
				{'media' === variation ? (
					<MediaEdit {...{ attributes, setAttributes, clientId }} ref={ref} />
				) : (
					<RichEdit
						{...{ attributes, setAttributes, clientId, isHideContent }}
						ref={ref}
					/>
				)}
			</>
		);
	},

	save: ({ attributes }) => {
		if ('media' === attributes.variation) {
			return null;
		}
		return <InnerBlocks.Content />;
	},
});
