/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useCallback } from '@wordpress/element';
import { PanelBody, ToggleControl, RangeControl } from '@wordpress/components';
import { PanelColorGradientSettings } from '@compatible';

/**
 * @Inner dependencies
 */
import ImageTab from './components/ImageTab';
import { LsSpaceControl, ArkbDeviceTab } from '@components';

/**
 * 設定
 */
const TEXT_COLORS = [
	{
		name: __('White', 'arkhe-blocks'),
		color: '#fff',
	},
	{
		name: __('Black', 'arkhe-blocks'),
		color: '#000',
	},
];

/**
 * export
 */
export default ({ attributes, setAttributes }) => {
	const {
		variation,
		media,
		mediaSP,
		focalPoint,
		focalPointSP,
		isRepeat,
		bgColor,
		bgGradient,
		opacity,
		textColor,
		paddingPC,
		paddingSP,
		filter,
	} = attributes;

	const mediaUrl = media.url;

	const setTextColor = useCallback(
		(newColor) => {
			setAttributes({ textColor: newColor });
		},
		[textColor]
	);

	const setOverlayColor = useCallback(
		(newColor) => {
			setAttributes({ bgColor: newColor || '' }); // undefinedの時、空文字にセット
			// 背景画像がなくカラー単体の変更時
			if (!mediaUrl && undefined === opacity) {
				setAttributes({ opacity: 100 });
			}
		},
		[setAttributes, opacity, mediaUrl]
	);

	const setGradientColor = useCallback(
		(newGradient) => {
			setAttributes({ bgGradient: newGradient || '' });
			// 背景画像がなくカラー単体の変更時
			if (!mediaUrl && undefined === opacity) {
				setAttributes({ opacity: 100 });
			}
		},
		[setAttributes, opacity, mediaUrl]
	);

	// Richスライダー用の設定
	const isRichSlider = 'rich' === variation;
	let richControls = null;
	if (isRichSlider) {
		richControls = (
			<>
				<PanelColorGradientSettings
					title={__('Color settings', 'arkhe-blocks')}
					initialOpen={true}
					settings={[
						{
							colorValue: textColor,
							colors: TEXT_COLORS,
							onColorChange: setTextColor,
							label: __('Text color', 'arkhe-blocks'),
						},
						{
							colorValue: bgColor,
							gradientValue: bgGradient,
							onColorChange: setOverlayColor,
							onGradientChange: setGradientColor,
							label: mediaUrl
								? __('Overlay color', 'arkhe-blocks')
								: __('Background color', 'arkhe-blocks'),
						},
					]}
				>
					<RangeControl
						label={
							mediaUrl
								? __('Overlay opacity', 'arkhe-blocks')
								: __('Background opacity', 'arkhe-blocks')
						}
						value={undefined === opacity ? 50 : opacity}
						min={0}
						max={100}
						onChange={(val) => {
							setAttributes({ opacity: val });
						}}
					/>
				</PanelColorGradientSettings>
				<PanelBody title={__('Padding settings', 'arkhe-blocks')}>
					<ArkbDeviceTab
						className='-padding'
						controlPC={
							<LsSpaceControl
								name='paddingPC'
								value={paddingPC}
								setAttributes={setAttributes}
								defaultOpen={true}
								nullValue={'0rem'}
								resetValue={{
									top: '2rem',
									left: '2rem',
									right: '2rem',
									bottom: '2rem',
								}}
							/>
						}
						controlSP={
							<LsSpaceControl
								name='paddingSP'
								value={paddingSP}
								setAttributes={setAttributes}
								successionHelp={__('PC setting', 'arkhe-blocks')}
								clearable={true}
								defaultOpen={true}
							/>
						}
					/>
				</PanelBody>
			</>
		);
	}

	return (
		<>
			<PanelBody
				title={
					isRichSlider
						? __('Background media setting', 'arkhe-blocks')
						: __('Media setting', 'arkhe-blocks')
				}
			>
				{isRepeat && mediaUrl && (
					<div className='arkb-imgPreview'>
						<img src={mediaUrl} alt='' />
					</div>
				)}
				<ImageTab
					attrs={{
						isRichSlider,
						media,
						mediaSP,
						focalPoint,
						focalPointSP,
						opacity,
					}}
					setAttributes={setAttributes}
				/>
				{isRichSlider && (
					<ToggleControl
						// フィルター設定。あとで数を増やせるように bool ではなく string で管理。
						label={__('Apply a dot filter', 'arkhe-blocks')}
						checked={'dot' === filter}
						onChange={(val) => {
							if (val) {
								setAttributes({ filter: 'dot' });
							} else {
								setAttributes({ filter: 'off' });
							}
						}}
						className='arkb-ctrl--mt--s arkb-ctrl--mb--xs'
						help={
							!window.arkbSettings.isArkhe
								? __(
										'This is only available when the theme is Arkhe.',
										'arkhe-blocks'
								  )
								: null
						}
					/>
				)}
			</PanelBody>
			{richControls}
		</>
	);
};
